.Logo {
  width: 100%;
  display: block;
  color: white;
  fill: currentColor;
  -webkit-transition: color .5s, fill .5s;
  -o-transition: color .5s, fill .5s;
  transition: color .5s, fill .5s; }
  .Logo-bomb-warning {
    -webkit-transform-origin: 9.9057% 50%;
        -ms-transform-origin: 9.9057% 50%;
            transform-origin: 9.9057% 50%; }
  .Logo:hover, .Logo:focus, .Logo:active {
    color: #ffc000; }
    .Logo:hover .Logo-bomb-warning, .Logo:focus .Logo-bomb-warning, .Logo:active .Logo-bomb-warning {
      -webkit-animation: spin 1.5s infinite linear;
              animation: spin 1.5s infinite linear; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes colorFade {
  0% {
    color: #ffc000; }
  50% {
    color: #ca1c05; }
  100% {
    color: #ffc000; } }

@keyframes colorFade {
  0% {
    color: #ffc000; }
  50% {
    color: #ca1c05; }
  100% {
    color: #ffc000; } }



