.MainSection {
  padding: 1.5rem;
  position: relative;
  z-index: 1; }
  .MainSection > * {
    display: block;
    width: 100%;
    margin: 0 auto;
    max-width: 1280px; }
  .MainSection-heading {
    margin-top: 1rem;
    -webkit-transition: opacity 2s;
    -o-transition: opacity 2s;
    transition: opacity 2s;
    -webkit-transition-delay: 1s;
         -o-transition-delay: 1s;
            transition-delay: 1s; }
  .MainSection-content {
    margin: 1rem auto;
    max-width: 768px;
    position: relative; }
    .MainSection-content.u-retroFont {
      font-size: 0.8rem; }
      @media screen and (min-width: 480px) {
        .MainSection-content.u-retroFont {
          font-size: 0.9rem; } }
      .MainSection-content.u-retroFont .MainSection-heading {
        margin: 2rem 0 1.5rem;
        font-size: 1.2rem; }
        @media screen and (min-width: 480px) {
          .MainSection-content.u-retroFont .MainSection-heading {
            font-size: 1.5rem; } }
    .MainSection-content-introText {
      padding-top: 1.5rem; }
    .MainSection-content-typingWrapper {
      position: absolute;
      top: 0; }
    .MainSection-content li {
      margin: 0.5rem 0; }
    .MainSection-content--cta {
      text-align: center; }
      .MainSection-content--cta Button {
        width: 80%;
        max-width: 400px; }
  .MainSection-features {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around; }
    .MainSection-features li {
      text-align: center;
      width: 80%;
      margin-bottom: 1rem; }
      @media screen and (min-width: 768px) {
        .MainSection-features li {
          width: 45%; } }
      .MainSection-features li p {
        text-align: left; }
    .MainSection-features h4 {
      font-size: 0.9rem;
      margin-bottom: 0.5rem; }
  .MainSection--alt {
    background: #011226;
    padding: 3rem 1.5rem; }
  .MainSection--fadeBg {
    background: -webkit-gradient(linear, left top, left bottom, from(#01264f), color-stop(50%, #01264f), to(#011226));
    background: -webkit-linear-gradient(top, #01264f, #01264f 50%, #011226);
    background: -o-linear-gradient(top, #01264f, #01264f 50%, #011226);
    background: linear-gradient(to bottom, #01264f, #01264f 50%, #011226); }
  .MainSection-headerImg, .MainSection-footerImg {
    position: absolute;
    left: 0;
    right: 0;
    max-width: none; }
  .MainSection-headerImg {
    top: 0; }
  .MainSection-footerImg {
    bottom: 0; }
  .MainSection:nth-last-child(2) .MainSection-footerImg {
    display: none; }



